<template>
  <el-row :gutter="20">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>{{ title }}</h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="module名称" prop="moduleName">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.moduleName"
              placeholder="请输入module名称"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content" style="padding: 5px;">
                例如已存在user服务，这里填写user服务目录下<br />
                go.mod文件里的module名称。
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="缓存名称" prop="cacheName">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.cacheName"
              placeholder="示例 userToken"
            ></el-input>
          </el-form-item>
          <el-form-item label="缓存key前缀名称" prop="prefixKey">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.prefixKey"
              placeholder="冒号结尾，示例 user:token:"
            ></el-input>
          </el-form-item>
          <el-form-item label="key名称" prop="keyName">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.keyName"
              placeholder="示例 id"
            ></el-input>
          </el-form-item>
          <el-form-item label="key类型" prop="keyType">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.keyType"
              placeholder="示例 uint64"
            ></el-input>
          </el-form-item>
          <el-form-item label="value名称" prop="valueName">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.valueName"
              placeholder="示例 token"
            ></el-input>
          </el-form-item>
          <el-form-item label="value的go类型" prop="valueType">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.valueType"
              placeholder="示例 string"
            ></el-input>
          </el-form-item>
        </el-form>
        <div style="margin: 30px auto;"></div>
        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">重置</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            >下载代码</el-button
          >
          <DownloadCode ref="downloadCode"></DownloadCode>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
export default {
  name: "WebCache",
  data() {
    return {
      title: "生成cache代码",
      path: "web-cache",
      helpInfo: `
      <p>把缓存代码移动到web或微服务目录下。</p>
      `,
      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,

      ruleForm: {
        moduleName: "",
        cacheName: "",
        prefixKey: "",
        keyName: "",
        keyType: "",
        valueName: "",
        valueType: "",
      },
      rules: {
        moduleName: [
          { required: true, message: "请输入go module名称", trigger: "blur" },
        ],
        cacheName: [
          { required: true, message: "请输入缓存名称", trigger: "blur" },
        ],
        prefixKey: [
          { required: true, message: "请输入缓存key前缀名称", trigger: "blur" },
        ],
        keyName: [
          { required: true, message: "请输入key名称", trigger: "blur" },
        ],
        keyType: [
          { required: true, message: "请输入key的go类型", trigger: "blur" },
        ],
        valueName: [
          { required: true, message: "请输入value名称", trigger: "blur" },
        ],
        valueType: [
          { required: true, message: "请输入value的go类型", trigger: "blur" },
        ],
      },
    };
  },
  created: function() {
    this.adaptiveWindows();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.moduleName = data.data.moduleName;
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const apiUrl = appConfig.spongeServiceAddr + "/generate";
          const arg = `web cache --module-name=${this.ruleForm.moduleName} --cache-name=${this.ruleForm.cacheName} --prefix-key=${this.ruleForm.prefixKey} --key-name=${this.ruleForm.keyName} --key-type=${this.ruleForm.keyType} --value-name=${this.ruleForm.valueName} --value-type=${this.ruleForm.valueType}`;
          this.$refs.downloadCode.execution(
            apiUrl,
            this.path,
            arg,
            this.helpInfo,
          );
        } else {
          return false;
        }
      });
    },
    clearFormData() {
      this.ruleForm = {};
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 50px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}
</style>



